import React, { Component } from 'react';
import 'antd-mobile/dist/antd-mobile.css';
import { Flex, Modal, Toast } from 'antd-mobile';
import './financial.css'
import Nav from '../../component/nav'
import i18n from '../../i18n';
import logo from '../../images/logo.png'
import worldshareblack from '../../images/worldshareblack.png'
import abi from '../../api/abi';
const alert = Modal.alert;

class Financial extends Component {

    constructor(props) {
        super(props);
        this.state = {
            account: {},
            IsWorldShare: true
        }
    }

    componentWillMount() {
        let self = this;
        let obj = JSON.parse(sessionStorage.getItem('account'));
        self.getUserInfo(obj.mainPKr);
        self.setState({
            account: obj
        })
    }
    getUserInfo = (mainPKr) => {
        let self = this;
        abi.getUserInfo(mainPKr, function (res) {
            if (res[0].state === "2") {
                self.setState({
                    IsWorldShare: true
                })
            } else {
                self.setState({
                    IsWorldShare: false
                })
            }
        })
    }
    goWorldShare(url) {
        let self = this;
        if (self.state.IsWorldShare) {
            this.props.history.push(url);
        } else {
            Toast.info(`${i18n.t("PleaseregisterKYCfirst")}!`)
        }
    }

    render() {
        return (
            <Nav selectedTab="3">
                <div className="tabcontent">
                    <Flex className="header">
                        <Flex.Item className="tabcontent-box">
                            <img className="logo" src={logo} alt="logo" />
                            <p className='title'>
                                DeFi{i18n.t("assetmanagement")}
                            </p>
                        </Flex.Item>
                    </Flex>
                </div>
                <div className="content">

                    <div className="financial" onClick={() => {
                        alert(`${i18n.t("Terms")}`, <div className="Terms">
                            <p className="Terms-title">1() </p>
                            <p>  GINKGO BANK         ,             .        ,           ,              . </p>
                            <p>  WORLDSHARE        WORLDSHARE      WORLDSHARE    DHAPY       .</p>
                            <p className="Terms-title">2()</p>
                            <p>1. GINKGO BANK         .</p>
                            <p>2. GINKGO BANK     , GINKGO BANK      1  1       . ,  ,   ,      .</p>
                            <p className="Terms-title">3()</p>
                            <p>1.GINKGO BANK           ,     . ,    GINKGO BANK       .</p>
                            <p>2.GINKGO BANK            . </p>
                            <p className="Terms-title">4( )</p>
                            <p> WS          .</p>
                            <p>1.KYC      GINKGO BANK   ( )   .</p>
                            <p>2.WS       WORLDSHARE     .</p>
                            <p className="Terms-title">5(  )</p>
                            <p>    , ,       .</p>
                            <p>                  .</p>
                            <p>1.   .</p>
                            <p>2. ,         , ,  .</p>
                            <p>3.             , ,  .</p>
                            <p>4. (        ,                 )   .</p>
                            <p>5.1 4   .</p>
                            <p>  3   ,  , ,         .</p>
                            <p className="Terms-title">6(   )</p>
                            <p> ( ), ,                .</p>
                            <p>          , , ,               .</p>
                            <p className="Terms-title">7()</p>
                            <p>       GINKGO BANK         .</p>
                            <p>1.            </p>
                            <p>2.   GINKGO BANK   </p>
                            <p>3. GINKGO BANK  </p>
                            <p className="Terms-title">8( )</p>
                            <p>1.GINKGO BANK       1          (          )    . ,     GINKGO BANK          .</p>
                            <p>2.1              1     .</p>
                            <p>3.GINKGO BANK 1  2                    ,           .     .</p>
                            <p className="Terms-title">9(  )</p>
                            <p>      ,               .</p>
                            <p>                 .</p>
                        </div>, [
                            { text: `${i18n.t("cancel")}`, onPress: () => console.log('cancel') },
                            {
                                text: `${i18n.t("agree")}`, onPress: () => {
                                    this.goWorldShare('/worldshare')
                                }
                            },
                        ])
                    }
                    }>
                        <div className="financial-box">
                            <div className="financial-header">
                                <p>WS  (210127_WS 1)</p>
                            </div>
                            <div className="financial-contet">
                                <img src={worldshareblack} />
                            </div>

                            <div className="financial-bottom">
                                <div className="financial-bottom-box">
                                    <div className="financial-bottom-left">
                                        <p>  </p>
                                    </div>
                                    <div className="financial-bottom-right">
                                        <p>GINKGOBANK  WORLDSHARE </p>
                                    </div>
                                </div>

                                <div className="financial-bottom-box">
                                    <div className="financial-bottom-left">
                                        <p> </p>
                                    </div>
                                    <div className="financial-bottom-right">
                                        <p>  DHAPY </p>
                                    </div>
                                </div>

                                <div className="financial-bottom-box">
                                    <div className="financial-bottom-left">
                                        <p> </p>
                                    </div>
                                    <div className="financial-bottom-right">
                                        <p>  30%  DHAPY</p>
                                    </div>
                                </div>
                                <div className="financial-bottom-box">
                                    <div className="financial-bottom-left">
                                        <p>DHAPY  </p>
                                    </div>
                                    <div className="financial-bottom-right">
                                        <p> 1000DKRW(,  5%)</p>
                                    </div>
                                </div>
                            </div>

                        </div>

                    </div>
                </div>
            </Nav>
        )
    }
}

export default Financial;
